import { ChatResponse, VehicleInfo, DiagnosisStage } from '../types';
import { getVehicleByVIN, getVehicleByPlate } from './vehicleService';

//  API  - POC 
const API_KEY = 'sk-a1f23477f6bf4e5e8dd0a672cdc0888c';
const BASE_URL = 'https://dashscope.aliyuncs.com/compatible-mode/v1';
const MODEL = 'qwen-max';

//  session ID
function generateSessionId(): string {
  return 'session_' + Date.now() + '_' + Math.random().toString(36).substr(2, 9);
}

// 
const SYSTEM_PROMPT = `

VIN

`;

export const chatApi = {
  async sendMessage(
    message: string,
    sessionId?: string,
    stage?: DiagnosisStage,
    onStream?: (content: string) => void
  ): Promise<ChatResponse> {
    const currentSessionId = sessionId || generateSessionId();

    // VIN
    let enhancedMessage = message;
    const vinRegex = /[A-HJ-NPR-Z0-9]{17}/i;
    const plateRegex = /[][A-Z][A-HJ-NP-Z0-9]{5,6}/;

    const vinMatch = message.match(vinRegex);
    const plateMatch = message.match(plateRegex);

    if (vinMatch) {
      const vehicle = getVehicleByVIN(vinMatch[0]);
      if (vehicle) {
        enhancedMessage = `VIN${vinMatch[0]}${JSON.stringify(vehicle)}`;
      }
    } else if (plateMatch) {
      const vehicle = getVehicleByPlate(plateMatch[0]);
      if (vehicle) {
        enhancedMessage = `${plateMatch[0]}${JSON.stringify(vehicle)}`;
      }
    }

    //  API
    const response = await fetch(`${BASE_URL}/chat/completions`, {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${API_KEY}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        model: MODEL,
        messages: [
          { role: 'system', content: SYSTEM_PROMPT },
          { role: 'user', content: enhancedMessage },
        ],
        stream: true,
        temperature: 0.7,
      }),
    });

    if (!response.ok) {
      throw new Error(`API : ${response.status}`);
    }

    const reader = response.body?.getReader();
    if (!reader) {
      throw new Error('');
    }

    const decoder = new TextDecoder();
    let fullContent = '';
    let buffer = '';

    while (true) {
      const { done, value } = await reader.read();
      if (done) break;

      buffer += decoder.decode(value, { stream: true });
      const lines = buffer.split('\n');
      buffer = lines.pop() || '';

      for (const line of lines) {
        if (line.startsWith('data:')) {
          const data = line.slice(5).trim();
          if (!data || data === '[DONE]') continue;

          try {
            const parsed = JSON.parse(data);
            const content = parsed.choices?.[0]?.delta?.content || '';
            if (content) {
              fullContent += content;
              onStream?.(content);
            }
          } catch {
            // 
          }
        }
      }
    }

    return {
      content: fullContent,
      sessionId: currentSessionId,
      stage: stage || 'perception',
    };
  },

  async getVehicleByVIN(vin: string): Promise<VehicleInfo | null> {
    return getVehicleByVIN(vin);
  },

  async getVehicleByPlate(plateNumber: string): Promise<VehicleInfo | null> {
    return getVehicleByPlate(plateNumber);
  },
};
